/* THis css file is for the styling of Loader.jsx */

.mainContainer {
  width: 100vw;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0%;
  left: 0%;
  flex-direction: column;
  align-items: center;
  z-index: 20;
  justify-content: center;
  background: rgba(20, 5, 54, 70%);
}
.ldsDefault {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.ldsDefault div {
  position: absolute;
  width: 6px;
  height: 6px;
  background: #0295cf;
  border-radius: 50%;
  animation: lds-default 1.2s linear infinite;
}
.ldsDefault div:nth-child(1) {
  animation-delay: 0s;
  top: 37px;
  left: 66px;
}
.ldsDefault div:nth-child(2) {
  animation-delay: -0.1s;
  top: 22px;
  left: 62px;
}
.ldsDefault div:nth-child(3) {
  animation-delay: -0.2s;
  top: 11px;
  left: 52px;
}
.ldsDefault div:nth-child(4) {
  animation-delay: -0.3s;
  top: 7px;
  left: 37px;
}
.ldsDefault div:nth-child(5) {
  animation-delay: -0.4s;
  top: 11px;
  left: 22px;
}
.ldsDefault div:nth-child(6) {
  animation-delay: -0.5s;
  top: 22px;
  left: 11px;
}
.ldsDefault div:nth-child(7) {
  animation-delay: -0.6s;
  top: 37px;
  left: 7px;
}
.ldsDefault div:nth-child(8) {
  animation-delay: -0.7s;
  top: 52px;
  left: 11px;
}
.ldsDefault div:nth-child(9) {
  animation-delay: -0.8s;
  top: 62px;
  left: 22px;
}
.ldsDefault div:nth-child(10) {
  animation-delay: -0.9s;
  top: 66px;
  left: 37px;
}
.ldsDefault div:nth-child(11) {
  animation-delay: -1s;
  top: 62px;
  left: 52px;
}
.ldsDefault div:nth-child(12) {
  animation-delay: -1.1s;
  top: 52px;
  left: 62px;
}
@keyframes lds-default {
  0%,
  20%,
  80%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
}
